export default {
	data() {
		return {
			news: []
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e){
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid='+newsid,
			});
		}
	}
}
